import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})

export class HeaderComponent implements OnInit {
  links: any[] = [
    {
      label: "overview",
      route: "/"
    },
    {
      label: "launches",
      route: "/launches"
    },
    {
      label: "rockets",
      route: "/rockets"
    },
    {
      label: "capsules",
      route: "/capsules"
    },
    {
      label: "launchpads",
      route: "/launchpads"
    },
    {
      label: "landpads",
      route: "/landpads"
    },
    {
      label: "crews",
      route: "/crews"
    },
    {
      label: "space x",
      route: "/about"
    }
  ];
  showMenu: boolean = false;
  active_link: string = "";
  screenWidth!: number;
  isOpen: boolean = false;
  logo_link: string ="assets/images/logo.png";

  constructor(private router: Router) { }

  ngOnInit(): void {
    this.router.events.subscribe(event => {
      if(event instanceof NavigationEnd )
      {
        this.getLocation(event.url);
      }
    });

    window.addEventListener('resize', this.setScreenWidth);
  }

  setScreenWidth():void {
    this.screenWidth = document.documentElement.clientWidth;
    if(this.screenWidth > 950){
      this.isOpen = false;
    }
  }

  getLocation(path: string): void {
    const lastSlash = path.lastIndexOf('/'); 
    let url;
    
    if(path === "/") {
      url = "home";
    }
    else if(lastSlash > 0) {
      url = path.slice(1, lastSlash);
    } else {
      if(path === '/about') {
        url = "space x";
      } else {
        url = path.slice(1, path.length);
      } 
    }
    this.active_link = url;
  }

  updateActiveLink(link: string): void {
    this.active_link = "";
    this.active_link = link;
    this.closeSidenav();
  }

  toggleOpen(): void {
    this.isOpen = !this.isOpen;
  }

  closeSidenav(): void {
    this.isOpen = false;
  }
}
