import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-paginator',
  templateUrl: './paginator.component.html',
  styleUrls: ['./paginator.component.scss']
})

export class PaginatorComponent implements OnInit {
  @Input() page!: number;
  @Input() itemsPerPage!: number;
  @Input() totalPages!: number;
  @Input() totalDocs!: number;
  @Input() hasPrevPage!: boolean;
  @Input() hasNextPage!: boolean;
  @Output() onNextPage = new EventEmitter();
  @Output() onPrevPage = new EventEmitter();
  @Output() onChangeLimit = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  nextPageRequest() {
    this.onNextPage.emit();
  }

  prevPageRequest() {
    this.onPrevPage.emit();
  }

  changeLimit(limit: number) {
    this.onChangeLimit.emit(Number(limit));
  }
}
