import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-rocketitem',
  templateUrl: './rocketitem.component.html',
  styleUrls: ['./rocketitem.component.scss']
})

export class RocketitemComponent implements OnInit {
  @Input() rocket!: any;
  rocket_link!: string;
  isLoading: boolean = true;

  constructor() { }

  ngOnInit(): void {
    this.rocket_link = `/rockets/${this.rocket.id}`;
  }

  toggleLoading(): void {
    this.isLoading = false;
  }
}
