import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';

const LAUNCHES_SUMMARY_QUERY = gql `
  query GetLaunchesSummary{
    launchesSummary{
      success
    }
  }
`;

const ROCKETS_SUMMARY_QUERY = gql `
  query GetRocketsSummary{
    rocketsSummary{
      active
    }
  }
`;

const CAPSULES_SUMMARY_QUERY = gql `
  query GetCapsulesSummary{
    capsulesSummary{
      status
    }
  }
`;

const LAUNCHPADS_SUMMARY_QUERY = gql `
  query GetLaunchpadsSummary{
    launchpadsSummary{
      status
    }
  }
`;

const LANDPADS_SUMMARY_QUERY = gql `
  query GetLandpadsSummary{
    landpadsSummary{
      status
    }
  }
`;

const CREWS_SUMMARY_QUERY = gql `
  query GetCrewsSummary{
    crewsSummary{
      agency
    }
  }
`;

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})

export class HomeComponent implements OnInit {
  summary: any;
  launches: any;
  rockets: any;
  capsules: any;
  launchpads: any;
  landpads: any;
  crews: any;
  loading: boolean = true;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo) { }

  ngOnInit(): void {
    this.getLaunchesSummary();
    this.getRocketsSummary();
    this.getCapsulesSummary();
    this.getLaunchpadsSummary();
    this.getLandpadsSummary();
    this.getCrewsSummary();
  }

  getLaunchesSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHES_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches = data.launchesSummary;
      });
  }

  getRocketsSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKETS_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets = data.rocketsSummary;
      });
  }

  getCapsulesSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULES_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.capsules = data.capsulesSummary;
      });
  }

  getLaunchpadsSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPADS_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpads = data.launchpadsSummary;
      });
  }

  getLandpadsSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LANDPADS_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.landpads = data.landpadsSummary;
      });
  }

  getCrewsSummary(): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CREWS_SUMMARY_QUERY,
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.crews = data.crewsSummary;
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
