import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-capsuleitem',
  templateUrl: './capsuleitem.component.html',
  styleUrls: ['./capsuleitem.component.scss']
})
export class CapsuleitemComponent implements OnInit {
  @Input() capsule!: any;
  capsule_link!: string;
  
  constructor() { }

  ngOnInit(): void {
    this.capsule_link = `/capsules/${this.capsule.id}`;
  }

}
