import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-launchitem',
  templateUrl: './launchitem.component.html',
  styleUrls: ['./launchitem.component.scss']
})

export class LaunchitemComponent implements OnInit {
  @Input() launch!: any;
  launch_link!: string;
  
  constructor() { }

  ngOnInit(): void {
    this.launch_link = `/launches/${this.launch.id}`;
  }

}
