import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

const CREW_QUERY = gql `
  query GetCrew($id: String!){
    crew(id: $id){
      name,
      status,
      image,
      agency,
      wikipedia,
      launches
    }
  }
`;

const LAUNCH_QUERY = gql `
  query GetLaunch($id: String!){
    launch(id: $id){
      id,
      name
    }
  }
`;

@Component({
  selector: 'app-crew',
  templateUrl: './crew.component.html',
  styleUrls: ['./crew.component.scss']
})

export class CrewComponent implements OnInit, OnDestroy {
  crew!: any;
  id!: string;
  loading!: boolean;
  launches: any = [];
  launchesLoaded: boolean = false;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo, private route: ActivatedRoute, private location: Location ) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });

    this.getCrew(this.id);
  }

  getCrew(id: string): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CREW_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.crew = data.crew;
        
        if(this.crew.launches.length > 0) {
          this.getLaunches(this.crew.launches);
        } else {
          if(this.crew.launches.length === this.launches.length) {
            this.launchesLoaded = true;
          }
        }
      });
  }

  getLaunches(launches: [string]): void{
    launches.map(launch => this.getLaunch(launch));
  }

  getLaunch(id: string): any{
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCH_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches.push({ id: data.launch.id, name: data.launch.name, link: `/launches/${data.launch.id}` });
        if(this.crew.launches.length === this.launches.length) {
          this.launchesLoaded = true;
        }
      });
  }

  goBack(): void {
    this.location.back();
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
