import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-capsules-summary',
  templateUrl: './capsules-summary.component.html',
  styleUrls: ['./capsules-summary.component.scss']
})
export class CapsulesSummaryComponent implements OnInit {
  @Input() capsules: any;
  total!: number;
  active!: number;
  destroyed!: number;
  retired!: number;
  unknown!: number;

  constructor() { }

  ngOnInit(): void {
    this.total = this.capsules.length;
    this.active = this.capsules.filter((capsule: any) => capsule.status === "active").length;
    this.destroyed = this.capsules.filter((capsule: any) => capsule.status === "destroyed").length;
    this.retired = this.capsules.filter((capsule: any) => capsule.status === "retired").length;
    this.unknown = this.capsules.filter((capsule: any) => capsule.status === "unknown").length;
  }

}
