import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-crews-summary',
  templateUrl: './crews-summary.component.html',
  styleUrls: ['./crews-summary.component.scss']
})

export class CrewsSummaryComponent implements OnInit {
  @Input() crews: any;
  total!: number;
  nasa!: number;
  jaxa!: number;
  esa!: number;
  
  constructor() { }

  ngOnInit(): void {
    this.total = this.crews.length;
    this.nasa = this.crews.filter((crew: any) => crew.agency === "NASA").length;
    this.jaxa = this.crews.filter((crew: any) => crew.agency === "JAXA").length;
    this.esa = this.crews.filter((crew: any) => crew.agency === "ESA").length;
  }

}
