import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

const LAUNCHPAD_QUERY = gql `
  query GetLaunchpad($id: String!){
    launchpad(id: $id){
      name,
      full_name,
      status,
      details,
      locality,
      region,
      launch_attempts,
      launch_successes,
      launches,
      rockets
    }
  }
`;

const LAUNCH_QUERY = gql `
  query GetLaunch($id: String!){
    launch(id: $id){
      id,
      name
    }
  }
`;

const ROCKET_QUERY = gql `
  query GetRocket($id: String!){
    rocket(id: $id){
      id,
      name
    }
  }
`;

@Component({
  selector: 'app-launchpad',
  templateUrl: './launchpad.component.html',
  styleUrls: ['./launchpad.component.scss']
})

export class LaunchpadComponent implements OnInit, OnDestroy {
  launchpad!: any;
  id!: string;
  loading: boolean = true;
  launches: any = [];
  rockets: any = [];
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo, private route: ActivatedRoute, private location: Location ) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });

    this.getLaunchpad(this.id);
  }

  getLaunchpad(id: string): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPAD_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpad = data.launchpad;
        
        if(this.launchpad.launches.length > 0) {
          this.getLaunches(this.launchpad.launches);
        } 

        if(this.launchpad.rockets.length > 0) {
          this.getRockets(this.launchpad.rockets);
        } 
      });
  }

  getLaunches(launches: [string]): void{
    launches.map(launch => this.getLaunch(launch));
  }

  getLaunch(id: string): any{
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCH_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches.push({ id: data.launch.id, name: data.launch.name, link: `/launches/${data.launch.id}` });
      });
  }

  getRockets(rockets: [string]): void{
    rockets.map(rocket => this.getRocket(rocket));
  }

  getRocket(id: string): any{
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKET_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets.push({ id: data.rocket.id, name: data.rocket.name, link: `/rockets/${data.rocket.id}` });
      });
  }
  
  goBack(): void {
    this.location.back();
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
