import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';

const LAUNCHPADS_QUERY = gql `
  query GetLaunchpads($page: Int!, $limit: Int!){
    launchpads(page: $page, limit: $limit){
      docs{
        id,
        name,
        status,
        locality,
        region
      },
      totalDocs,
      limit
      page,
      totalPages,
      hasPrevPage,
      hasNextPage
    }
  }
`;

@Component({
  selector: 'app-launchpads',
  templateUrl: './launchpads.component.html',
  styleUrls: ['./launchpads.component.scss']
})

export class LaunchpadsComponent implements OnInit, OnDestroy {
  launchpads: any;
  loading: boolean = true;
  // pagination variables
  page!: number;
  totalPages!: number;
  totalDocs!: number;
  itemsPerPage: number = 5;
  hasPrevPage!: boolean;
  hasNextPage!: boolean;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo) { }

  ngOnInit(): void {
    if(!this.page) {
      this.page = 1;
    }

    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPADS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpads = data.launchpads.docs;
        this.updatePaginatorValues(data);
      });
  }

  updatePaginatorValues(data: any):void {
    this.hasPrevPage = data.launchpads.hasPrevPage;
    this.hasNextPage = data.launchpads.hasNextPage;
    this.totalPages = data.launchpads.totalPages;
    this.totalDocs = data.launchpads.totalDocs;
  }

  changeLimit(newLimit: number): void {
    this.itemsPerPage = newLimit;
    this.loading = true;
    this.page = 1;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPADS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpads = data.launchpads.docs;
        this.updatePaginatorValues(data);
      });
  }

  nextPage(): void {
    this.loading = true;
    this.page++;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPADS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpads = data.launchpads.docs;
        this.updatePaginatorValues(data);
      });
  }

  prevPage(): void {
    this.loading = true;
    this.page--;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPADS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpads = data.launchpads.docs;
        this.updatePaginatorValues(data);
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
