import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';

const LAUNCHES_QUERY = gql `
  query GetLaunches($page: Int!, $limit: Int!){
    launches(page: $page, limit: $limit){
      docs{
        id,
        flight_number,
        name,
        date_local,
        success
      },
      totalDocs,
      limit
      page,
      totalPages,
      hasPrevPage,
      hasNextPage
    }
  }
`;

@Component({
  selector: 'app-launches',
  templateUrl: './launches.component.html',
  styleUrls: ['./launches.component.scss']
})

export class LaunchesComponent implements OnInit, OnDestroy {
  launches: any;
  loading: boolean = true;
  // pagination variables
  page!: number;
  totalPages!: number;
  totalDocs!: number;
  itemsPerPage: number = 5;
  hasPrevPage!: boolean;
  hasNextPage!: boolean;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo) { }

  ngOnInit(): void {
    if(!this.page) {
      this.page = 1;
    }

    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches = data.launches.docs;
        this.updatePaginatorValues(data);
      });
  }

  updatePaginatorValues(data: any):void {
    this.hasPrevPage = data.launches.hasPrevPage;
    this.hasNextPage = data.launches.hasNextPage;
    this.totalPages = data.launches.totalPages;
    this.totalDocs = data.launches.totalDocs;
  }

  changeLimit(newLimit: number): void {
    this.itemsPerPage = newLimit;
    this.loading = true;
    this.page = 1;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches = data.launches.docs;
        this.updatePaginatorValues(data);
      });
  }

  nextPage(): void {
    this.loading = true;
    this.page++;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches = data.launches.docs;
        this.updatePaginatorValues(data);
      });
  }

  prevPage(): void {
    this.loading = true;
    this.page--;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches = data.launches.docs;
        this.updatePaginatorValues(data);
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
