import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-launches-summary',
  templateUrl: './launches-summary.component.html',
  styleUrls: ['./launches-summary.component.scss']
})

export class LaunchesSummaryComponent implements OnInit {
  @Input() launches: any;
  total!: number;
  upcoming!: number;
  successful!: number;
  failed!: number;
  success_rate!: number;

  constructor() { }

  ngOnInit(): void {
    this.total = this.launches.length;
    this.upcoming = this.launches.filter((launch: any) => launch.success === null).length;
    this.successful = this.launches.filter((launch: any) => launch.success === true).length;
    this.failed = this.launches.filter((launch: any) => launch.success === false).length;
    this.success_rate = Math.floor(( this.successful/ ( this.successful + this.failed )) * 100);
  }
}
