import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-launchpads-summary',
  templateUrl: './launchpads-summary.component.html',
  styleUrls: ['./launchpads-summary.component.scss']
})

export class LaunchpadsSummaryComponent implements OnInit {
  @Input() launchpads: any;
  total!: number;
  active!: number;
  retired!: number;
  under_construction!: number;
  
  constructor() { }

  ngOnInit(): void {
    this.total = this.launchpads.length;
    this.active = this.launchpads.filter((launchpad: any) => launchpad.status === "active").length;
    this.retired = this.launchpads.filter((launchpad: any) => launchpad.status === "retired").length;
    this.under_construction = this.launchpads.filter((launchpad: any) => launchpad.status === "under construction").length;
  }

}
