import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';

const ROCKETS_QUERY = gql `
  query GetRockets($page: Int!, $limit: Int!){
    rockets(page: $page, limit: $limit){
      docs{
        id,
        name,
        active,
        first_flight,
        success_rate_pct
      },
      totalDocs,
      limit
      page,
      totalPages,
      hasPrevPage,
      hasNextPage
    }
  }
`;

@Component({
  selector: 'app-rockets',
  templateUrl: './rockets.component.html',
  styleUrls: ['./rockets.component.scss']
})

export class RocketsComponent implements OnInit, OnDestroy {
  rockets: any;
  loading: boolean = true;
  // pagination variables
  page!: number;
  totalPages!: number;
  totalDocs!: number;
  itemsPerPage: number = 5;
  hasPrevPage!: boolean;
  hasNextPage!: boolean;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo) { }

  ngOnInit(): void {
    if(!this.page) {
      this.page = 1;
    }

    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKETS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets = data.rockets.docs;
        this.updatePaginatorValues(data);
      });
  }

  updatePaginatorValues(data: any):void {
    this.hasPrevPage = data.rockets.hasPrevPage;
    this.hasNextPage = data.rockets.hasNextPage;
    this.totalPages = data.rockets.totalPages;
    this.totalDocs = data.rockets.totalDocs;
  }

  changeLimit(newLimit: number): void {
    this.itemsPerPage = newLimit;
    this.loading = true;
    this.page = 1;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKETS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets = data.rockets.docs;
        this.updatePaginatorValues(data);
      });
  }

  nextPage(): void {
    this.loading = true;
    this.page++;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKETS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets = data.rockets.docs;
        this.updatePaginatorValues(data);
      });
  }

  prevPage(): void {
    this.loading = true;
    this.page--;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKETS_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rockets = data.rockets.docs;
        this.updatePaginatorValues(data);
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
