import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-launchpaditem',
  templateUrl: './launchpaditem.component.html',
  styleUrls: ['./launchpaditem.component.scss']
})

export class LaunchpaditemComponent implements OnInit {
  @Input() launchpad!: any;
  launchpad_link!: string;
  
  constructor() { }

  ngOnInit(): void {
    this.launchpad_link = `/launchpads/${this.launchpad.id}`;
  }
}
