import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-rockets-summary',
  templateUrl: './rockets-summary.component.html',
  styleUrls: ['./rockets-summary.component.scss']
})

export class RocketsSummaryComponent implements OnInit {
  @Input() rockets: any;
  total!: number;
  active!: number;
  inactive!: number;

  constructor() { }

  ngOnInit(): void {
    this.total = this.rockets.length;
    this.active = this.rockets.filter((rocket: any) => rocket.active === true).length;
    this.inactive = this.rockets.filter((rocket: any) => rocket.active === false).length;
  }

}
