import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './components/about/about.component';
import { LaunchesComponent } from './components/launches/launches.component';
import { LaunchComponent } from './components/launch/launch.component';
import { HomeComponent } from './components/home/home.component';
import { RocketsComponent } from './components/rockets/rockets.component';
import { RocketComponent } from './components/rocket/rocket.component';
import { CapsulesComponent } from './components/capsules/capsules.component';
import { CapsuleComponent } from './components/capsule/capsule.component';
import { CrewsComponent } from './components/crews/crews.component';
import { CrewComponent } from './components/crew/crew.component';
import { LaunchpadsComponent } from './components/launchpads/launchpads.component';
import { LaunchpadComponent } from './components/launchpad/launchpad.component';
import { LandpadsComponent } from './components/landpads/landpads.component';
import { LandpadComponent } from './components/landpad/landpad.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'about', component: AboutComponent },
  { path: 'launches', component: LaunchesComponent },
  { path: 'launches/:id', component: LaunchComponent },
  { path: 'launchpads', component: LaunchpadsComponent },
  { path: 'launchpads/:id', component: LaunchpadComponent },
  { path: 'landpads', component: LandpadsComponent },
  { path: 'landpads/:id', component: LandpadComponent },
  { path: 'rockets', component: RocketsComponent },
  { path: 'rockets/:id', component: RocketComponent },
  { path: 'capsules', component: CapsulesComponent },
  { path: 'capsules/:id', component: CapsuleComponent },
  { path: 'crews', component: CrewsComponent },
  { path: 'crews/:id', component: CrewComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
