import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { Location } from '@angular/common';

const LAUNCH_QUERY = gql `
  query GetLaunches($id: String!){
    launch(id: $id){
      flight_number,
      name,
      details,
      date_local,
      success,
      rocket,
      launchpad,
      links {
        article,
        webcast,
        wikipedia
      }
    }
  }
`;

const ROCKET_QUERY = gql `
  query GetRocket($id: String!){
    rocket(id: $id){
      id,
      name
    }
  }
`;

const LAUNCHPAD_QUERY = gql `
  query GetLaunchpad($id: String!){
    launchpad(id: $id){
      id,
      full_name
    }
  }
`;

@Component({
  selector: 'app-launch',
  templateUrl: './launch.component.html',
  styleUrls: ['./launch.component.scss']
})

export class LaunchComponent implements OnInit, OnDestroy {
  launch!: any;
  id!: string;
  rocket: any = {}; 
  launchpad: any = {}; 
  video_link!: string;
  trustedUrl!: any;
  loading!: boolean;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo, private route: ActivatedRoute, private sanitizer: DomSanitizer, private location: Location) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });
    
    this.getLaunch();  
  }

  getLaunch() {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCH_QUERY,
      variables: {
        id: this.id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launch = data.launch;
        this.video_link = this.launch.links.webcast;
        this.trustedUrl = this.sanitizer.bypassSecurityTrustResourceUrl(this.video_link);
        this.getRocket(data.launch.rocket);
        this.getLaunchpad(data.launch.launchpad);
      });
  }

  getRocket(id: string) {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKET_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rocket = { id: data.rocket.id, name: data.rocket.name, link: `/rockets/${data.rocket.id}` };
      });
  }

  getLaunchpad(id: string) {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCHPAD_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launchpad = { id: data.launchpad.id, name: data.launchpad.full_name, link: `/launchpads/${data.launchpad.id}` };
      });
  }

  goBack(): void {
    this.location.back();
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
