import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

const LANDPAD_QUERY = gql `
  query GetLandpad($id: String!){
    landpad(id: $id){
      id,
      name,
      full_name,
      status,
      details,
      locality,
      region,
      landing_attempts,
      landing_successes,
      wikipedia,
      launches
    }
  }
`;

const LAUNCH_QUERY = gql `
  query GetLaunch($id: String!){
    launch(id: $id){
      id,
      name
    }
  }
`;

@Component({
  selector: 'app-landpad',
  templateUrl: './landpad.component.html',
  styleUrls: ['./landpad.component.scss']
})

export class LandpadComponent implements OnInit, OnDestroy {
  landpad!: any;
  id!: string;
  loading!: boolean;
  launches: any = [];
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo, private route: ActivatedRoute, private location: Location ) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });

    this.getLandpad(this.id);
  }

  getLandpad(id: string): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LANDPAD_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.landpad = data.landpad;
        
        if(this.landpad.launches.length > 0) {
          this.getLaunches(this.landpad.launches);
        }
      });
  }

  getLaunches(launches: [string]): void{
    launches.map(launch => this.getLaunch(launch));
  }

  getLaunch(id: string): any{
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCH_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches.push({ id: data.launch.id, name: data.launch.name, link: `/launches/${data.launch.id}` });
      });
  }

  goBack(): void {
    this.location.back();
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
