import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

const ROCKET_QUERY = gql `
  query GetRocket($id: String!){
    rocket(id: $id){
      name,
      active,
      description,
      first_flight,
      success_rate_pct,
      stages,     
      boosters,
      flickr_images,
      height{
        meters,
        feet
      },
      diameter{
        meters,
        feet
      },
      mass{
        kg,
        lb
      },
      wikipedia
    }
  }
`;

@Component({
  selector: 'app-rocket',
  templateUrl: './rocket.component.html',
  styleUrls: ['./rocket.component.scss']
})

export class RocketComponent implements OnInit, OnDestroy {
  rocket!: any;
  id!: string;
  loading!: boolean;
  isLoadingImages: boolean = true;
  images!: any;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo, private route: ActivatedRoute, private location: Location) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });

    this.getRocket(this.id);
  }

  getRocket(id: string) {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: ROCKET_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.rocket = data.rocket;
      });
  }

  toggleLoading(): void {
    this.isLoadingImages = false;
  }

  goBack(): void {
    this.location.back();
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
