import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';

const CAPSULES_QUERY = gql `
  query GetCapsules($page: Int!, $limit: Int!){
    capsules(page: $page, limit: $limit){
      docs{
        id,
        serial,
        status,
        type,
        launches
      },
      totalDocs,
      limit
      page,
      totalPages,
      hasPrevPage,
      hasNextPage
    }
  }
`;

// const CAPSULES_QUERY = gql `
//   query GetCapsules{
//     capsules{
//       id,
//       serial,
//       status,
//       type,
//       launches
//     }
//   }
// `;

@Component({
  selector: 'app-capsules',
  templateUrl: './capsules.component.html',
  styleUrls: ['./capsules.component.scss']
})

export class CapsulesComponent implements OnInit {
  capsules: any;
  loading: boolean = true;
  // pagination variables
  page!: number;
  totalPages!: number;
  totalDocs!: number;
  itemsPerPage: number = 5;
  hasPrevPage!: boolean;
  hasNextPage!: boolean;
  private querySubscription!: Subscription;

  constructor(private apollo: Apollo) { }

  ngOnInit(): void {
    if(!this.page) {
      this.page = 1;
    }

    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({data, loading}) => {
        this.loading = loading;
        this.capsules = data.capsules.docs;
        this.updatePaginatorValues(data);
      })
  }

  updatePaginatorValues(data: any):void {
    this.hasPrevPage = data.capsules.hasPrevPage;
    this.hasNextPage = data.capsules.hasNextPage;
    this.totalPages = data.capsules.totalPages;
    this.totalDocs = data.capsules.totalDocs;
  }

  changeLimit(newLimit: number): void {
    this.itemsPerPage = newLimit;
    this.loading = true;
    this.page = 1;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.capsules = data.capsules.docs;
        this.updatePaginatorValues(data);
      });
  }

  nextPage(): void {
    this.loading = true;
    this.page++;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.capsules = data.capsules.docs;
        this.updatePaginatorValues(data);
      });
  }

  prevPage(): void {
    this.loading = true;
    this.page--;
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULES_QUERY,
      variables: {
        page: this.page,
        limit: this.itemsPerPage
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.capsules = data.capsules.docs;
        this.updatePaginatorValues(data);
      });
  }

  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
