import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-landpads-summary',
  templateUrl: './landpads-summary.component.html',
  styleUrls: ['./landpads-summary.component.scss']
})

export class LandpadsSummaryComponent implements OnInit {
  @Input() landpads: any;
  total!: number;
  active!: number;
  retired!: number;
  under_construction!: number;

  constructor() { }

  ngOnInit(): void {
    this.total = this.landpads.length;
    this.active = this.landpads.filter((landpad: any) => landpad.status === "active").length;
    this.retired = this.landpads.filter((landpad: any) => landpad.status === "retired").length;
    this.under_construction = this.landpads.filter((landpad: any) => landpad.status === "under construction").length;
  }

}
