import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { Apollo, gql } from 'apollo-angular';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';

const CAPSULE_QUERY = gql `
  query GetCapsule($id: String!){
    capsule(id: $id){
      serial,
      status,
      type,
      last_update,
      reuse_count,
      land_landings,     
      water_landings,
      launches
    }
  }
`;

const LAUNCH_QUERY = gql `
  query GetLaunch($id: String!){
    launch(id: $id){
      id,
      name
    }
  }
`;

@Component({
  selector: 'app-capsule',
  templateUrl: './capsule.component.html',
  styleUrls: ['./capsule.component.scss']
})

export class CapsuleComponent implements OnInit, OnDestroy {

  capsule!: any;
  id!: string;
  loading!: boolean;
  launches: any = [];
  launchesLoaded: boolean = false;
  private querySubscription!: Subscription; 

  constructor(private apollo: Apollo, private route: ActivatedRoute, private location: Location ) { }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.id = params.id;
    });
    
    this.getCapsule(this.id);
  }

  getCapsule(id: string): void {
    this.querySubscription = this.apollo.watchQuery<any>({
      query: CAPSULE_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.capsule = data.capsule;
        if(this.capsule.launches.length > 0) {
          this.getLaunches(this.capsule.launches);
        } else {
          if(this.capsule.launches.length === this.launches.length) {
            this.launchesLoaded = true;
          }
        }
      });
  }

  getLaunches(launches: [string]): void{
    launches.map(launch => this.getLaunch(launch))
  }

  getLaunch(id: string): any{
    this.querySubscription = this.apollo.watchQuery<any>({
      query: LAUNCH_QUERY,
      variables: {
        id
      }
    })
      .valueChanges
      .subscribe(({ data, loading }) => {
        this.loading = loading;
        this.launches.push({ id: data.launch.id, name: data.launch.name, link: `/launches/${data.launch.id}` });
        if(this.capsule.launches.length === this.launches.length) {
          this.launchesLoaded = true;
        }
      });
  }

  goBack(): void {
    this.location.back();
  }
  
  ngOnDestroy() {
    this.querySubscription.unsubscribe();
  }
}
