import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-crewitem',
  templateUrl: './crewitem.component.html',
  styleUrls: ['./crewitem.component.scss']
})

export class CrewitemComponent implements OnInit {
  @Input() crew!: any;
  crew_link!: string;
  
  constructor() { }

  ngOnInit(): void {
    this.crew_link = `/crews/${this.crew.id}`;
  }
}
