import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-landpaditem',
  templateUrl: './landpaditem.component.html',
  styleUrls: ['./landpaditem.component.scss']
})
export class LandpaditemComponent implements OnInit {
  @Input() landpad!: any;
  landpad_link!: string;
  
  constructor() { }

  ngOnInit(): void {
    this.landpad_link = `/landpads/${this.landpad.id}`;
  }
}
